import React, { useState } from "react";
import './login.css';
import { Route, Link, useHistory } from 'react-router-dom';

function Login() {
    const [userId, setUserId] = useState('');
    const [userNum, setUserNum] = useState('');

    const onChangeId = (e) => {
        setUserId(e.target.value);
    }
    const onChangeNum = (e) => {
        setUserNum(e.target.value);
    }

    //    
    const onResetId = () => {
        setUserId('');
    }
    //     
    const onResetNum = () => {
        setUserNum('');
    }

    
    //   
    const [pwStatus, setPwStatus] = useState("password");
    const onChangPw = () => {
        if(pwStatus == "password") {
            setPwStatus("text");
        }else {
            setPwStatus("password");
        }
    }
    //   
    const [numStatus, setNumPwStatus] = useState("password");
    const onChangNum = () => {
        if(numStatus == "password") {
            setNumPwStatus("text");
        }else {
            setNumPwStatus("password");
        }
    }

    //    
    
    const showOrder = () => {
        const box1 = document.querySelector('.guest_order');
        const box2 = document.querySelector('.guest_order_history');
        const guestBtn = document.querySelectorAll('.guest_btn ul li');
        
        box1.style.display = "block";
        box2.style.display = "none";

        guestBtn[0].classList.add("on");
        guestBtn[1].classList.remove("on");
    }
    const showOrderHistory = () => {
        const box1 = document.querySelector('.guest_order');
        const box2 = document.querySelector('.guest_order_history');
        const guestBtn = document.querySelectorAll('.guest_btn ul li');

        box1.style.display = "none";
        box2.style.display = "block";

        guestBtn[0].classList.remove("on");
        guestBtn[1].classList.add("on");
    }

    //  
    const [saveId, setSaveId] = useState(true);
    const [autoLogin, setAutoLogin] = useState(true);
    const onIdSave = e => {
        e.preventDefault();
        
        if(saveId == false) {
            document.querySelectorAll('.check label')[0].classList.remove('on');
        }else {
            document.querySelectorAll('.check label')[0].classList.add('on');
        }
        setSaveId(!saveId);
    }
    const onAutoLogin = e => {
        e.preventDefault();
        
        if(autoLogin == false) {
            document.querySelectorAll('.check label')[1].classList.remove('on');
        }else {
            document.querySelectorAll('.check label')[1].classList.add('on');
        }
        setAutoLogin(!autoLogin);
    }


    const ready = () => {
        alert('.');
    }

    return(
        <div>
            {/*  */}
            <div className="member">
                <div className="member_in">
                    <div className="member_title">
                        <em>YOUR WAY</em><br/>
                        <strong>! .</strong>
                    </div>
                    <div className="input_form">
                        {/*  */}
                        <div className="normal_login">
                            <p> </p>
                            <div className="login_input">
                                <div className="input_box">
                                    <input type="text" name="id" value={userId} onChange={onChangeId} placeholder=" ()" />
                                    <button className="input_x" onClick={onResetId}></button>
                                </div>
                                <div className="input_box">
                                    <input type={pwStatus} name="pw" placeholder="" />
                                    <button className="input_view" onClick={onChangPw}></button>
                                </div>
                                <div className="check">
                                    <label onClick={onIdSave}><input type="checkbox" /><span> </span></label>
                                    <label onClick={onAutoLogin}><input type="checkbox" /><span> </span></label>
                                </div>
                                <div className="btn">
                                    <div><button onClick={ready}></button></div>
                                    <div><Link to="/join" className="join_btn"></Link></div>
                                </div>
                                <div className="search">
                                    <ul>
                                        <li onClick={ready}><Link to="/login" className="searchBtn"> </Link></li>
                                        <li onClick={ready}><Link to="/login" className="searchBtn"> </Link></li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        {/*   */}
                        <div className="easy_login">
                            <p></p>
                            <ul>
                                <li onClick={ready}><a href="#"><span> </span></a></li>
                                <li onClick={ready}><a href="#"><span> </span></a></li>
                                <li onClick={ready}><a href="#"><span> </span></a></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>

            {/*  */}
            <div className="guest">
                <div className="guest_in">
                    <div className="guest_btn">
                        <ul>
                            <li className="on"><button onClick={showOrder}><span> </span></button></li>
                            <li><button onClick={showOrderHistory}><span> </span></button></li>
                        </ul>
                    </div>

                    <div>
                        {/*   */}
                        <div className="guest_order">
                            <div className="guest_box">
                                <p>    .</p>
                            </div>

                            <div className="guest_box_btn">
                                <p onClick={ready}><Link to="/login" className="guestBtn"> </Link></p>
                            </div>
                        </div>
                        {/*   */}
                        <div className="guest_order_history" style={{display: "none"}}>
                            <div className="guest_box">
                                <div className="guest_input_box">
                                    <p></p>
                                    <div>
                                        <input type="text" placeholder=" " value={userNum} onChange={onChangeNum} />
                                        <button className="input_x" onClick={onResetNum}></button>
                                    </div>
                                </div>
                                <div className="guest_input_box">
                                    <p></p>
                                    <div>
                                        <input type={numStatus} placeholder="" />
                                        <button className="input_view" onClick={onChangNum}></button>
                                    </div>
                                </div>
                            </div>

                            <div className="guest_box_btn">
                                <p onClick={ready}><Link to="/login" className="guestBtn"></Link></p>
                            </div>
                        </div>
                    </div>
                    
                </div>
            </div>
        </div>
    );
}

export default Login;